import React,{Component} from 'react';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import {Container, Row, Col} from 'reactstrap';
import {Form, FormGroup, Label, Input, FormText, Button} from 'reactstrap';




import "./AddProductCss.css";

class AddProductScreen extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
          BrandName:'',
          ProductName:'',
          ProductPrice:'',
          Model:'',
          Ram:'',
          Storage:''

    
      }

      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);

    };

    handleChange = (event) => {
        this.setState({
            [event.target.name] : event.target.value
        })
    }


    handleSubmit = (event) => {
        event.preventDefault();
        const formData = {
            BrandName:this.state.BrandName,
            ProductName:this.state.ProductName,
            ProductPrice:this.state.ProductPrice,
            Model:this.state.Model,
            Ram:this.state.Ram,
            Storage:this.state.Storage
        }
    }
  
            

            
        

   


  render(props) {
    return (

    <div id="main-addproduct-container">
    <div id="addproduct">
    <div>
        <h3>Add Product !</h3>

        <form  onSubmit= {this.handleSubmit}>

        <label>Brand Name
        <input type="text" name="BrandName" value={this.state.BrandName} onChange={this.handleChange}/>
        </label>
        
        <label>Product Name
        <input type="text" name="ProductName" value={this.state.ProductName} onChange={this.handleChange}/>
        </label>
    
        <label>Product Price
        <input type="text" name="ProductPrice" value={this.state.ProductPrice} onChange={this.handleChange}/>
        </label>

        <label>Model
        <input type="text" name="Model" value={this.state.Model} onChange={this.handleChange}/>
        </label>

        <label>Ram
        <input type="text" name="Ram" value={this.state.Ram} onChange={this.handleChange}/>
        </label>

        <label>Storage
        <input type="text" name="Storage" value={this.state.Storage} onChange={this.handleChange}/>
        </label>



        <input style={{marginLeft:'-10px'}}type='submit' className="button ab"  value='add product'/>

        
       
        </form>
    
   </div> 
   </div>
   </div>
   


      );
  }
}
 
export default AddProductScreen;
